<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>>
        <el-table-column label="" prop="pay_status">
          <template v-slot:default="scope">
            <el-tag type="syccess" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>>
        <el-table-column label="" prop="create_time">
          <template v-slot:default="scope">
            {{ scope.row.create_time | dataFormat }}
          </template> </el-table-column
        >>
        <el-table-column label="">
          <template v-slot:default="">
            <el-button
              icon="el-icon-edit"
              type="primary"
              size="mini"
              @click="eidtAddress"
            ></el-button>
            <el-button
              icon="el-icon-location"
              type="success"
              size="mini"
              @click="showProgressBox"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="adderssClose"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :props="{ expandTrigger: 'hover' }"
            :options="cityData"
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressVisible"
      width="50%"
      
      @close="progressClose"
    >
      <el-timeline :reverse="true">
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.ftime"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="progressVisible = false"> </el-button>
        <el-button type="primary" @click="progressVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cityData from "assets/js/citydata";
import wuliuData from "assets/js/wuliu"
export default {
  name: "Order",
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      // 
      total: 0,

      // 
      orderList: [],

      editDialogVisible: false,
      addressForm: {
        address1: [],
        address2: "",
      },
      addressFormRules: {
        address1: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        address2: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      cityData,
      progressVisible: false,
      progressInfo: [],
    };
  },
  created() {
    this.getOrderList();
    
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get("/orders", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.orderList = res.data.goods;
      this.total = res.data.total;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOrderList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getOrderList();
    },
    // 
    eidtAddress() {
      this.editDialogVisible = true;
    },
    adderssClose() {
      this.$refs.addressFormRef.resetFields();
    },
    async showProgressBox() {
      this.progressInfo = wuliuData.data
      this.progressVisible = true;
      
    },
    progressClose() {

    }
  },
};
</script>

<style scoped>
.el-cascader {
  width: 100%;
}
</style>